body {
  margin: 0;
}

img {
  border-radius: 10px 10px 0 0;
  display: block;
}

.search-form {
  background-image: linear-gradient(#00f, #ff0);
  justify-content: center;
  gap: 2px;
  padding-top: 70px;
  padding-bottom: 70px;
  display: flex;
}

input {
  height: 24px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  padding: 10px;
  display: flex;
}

.info {
  border: 1px solid #000;
  border-top: none;
  border-radius: 0 0 10px 10px;
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  display: flex;
}

.photo-card {
  transition: transform .35s;
}

.photo-card:hover {
  transform: scale(1.03);
  box-shadow: 1px 1px 20px #000;
}

.load-more-hidden {
  display: none;
}

.load-more {
  color: #fff;
  background-color: #6495ed;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 30px;
  font-size: 20px;
  display: block;
}

.load-more:hover {
  background-color: #4169e1;
  box-shadow: 1px 1px 5px #000;
}

